<script>
import LeadDataService from "../../../services/DataService";
import DynamicForm from "../../../components/Common/DynamicForm.vue";

export default {
    components: {
        DynamicForm
    },
    data() {
      
        return {
            isLoading:false,
            LeadData: null,
            LeadOwner: null,
            GroupBy: [],
            Channel: null,
            IndustryType: null,
            Status: null,
            options: {
                format: 'MM-dd-yyyy',
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                maxDate: new Date()
            },
            DependentOption: [{
                format: 'MM-dd-yyyy',
                useCurrent: false,
                showClear: true,
                showClose: true,
                allowInputToggle: true,
                minDate: new Date()
            }
            ],
            vendorPurchaseOrderList: [],
            tempVendorPurchaseOrderList: [],
            selectedPurchaseOrder: [],
            purchaseOrderDetails: null,
            isShowAddNewProduct: true,
            selected: false,
            isTaxNameFilled: false,
            selectoptions: [],
            submited: false,
            inward_id: this.$route.params.id,
            dateformat:null, 
            userId:null,
            companyId:null  
        }
    },
    created: function () {
        this.userId= this.GetUserInfo.ID;
        this.companyId= this.GetUserInfo.CompanyId;
        this.dateformat=this.GetUserInfo.DateFormat;
        Number.prototype.toFixed2 = function () {
            var amount = Math.floor(parseFloat(this, 10) * 100) / 100;
            return parseFloat(amount, 10).toFixed(2);
        };
        this.FetchData();
    },
    directives: {
        'click-outside': {
            bind: function (el, binding, vNode) {
              
                if (typeof binding.value !== 'function') {
                    const compName = vNode.context.name
                    let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                    if (compName) { warn += `Found in component '${compName}'` }
                    console.warn(warn)
                }
               
                const bubble = binding.modifiers.bubble
                const handler = (e) => {
                    if (bubble || (!el.contains(e.target) && el !== e.target)) {
                        binding.value(e)
                    }
                }
                el.__vueClickOutside__ = handler
                
                document.addEventListener('click', handler)
            },
            unbind: function (el, binding) {
               
                document.removeEventListener('click', el.__vueClickOutside__)
                el.__vueClickOutside__ = null
            }
        }
    },
    methods: {
        onCancel: function () {
            this.$router.push({ name: 'InwardList' });
        },
        AssignToggleValues: function (event, item, array, parentIndex, index) {
            var vm = this;
            var value = "";
            if (event.target.checked) {
                var chkItem = $.grep(array, function (element, index) {
                    return (String(element.value).toLowerCase() == 'yes' || String(element.value).toLowerCase() == '1' ||
                        String(element.value).toLowerCase() == 'true' || String(element.value).toLowerCase() == 'public' || String(element.value).toLowerCase() == 'asset');
                });
                if (chkItem != null && chkItem.length > 0) {
                    if (typeof chkItem[0] === 'object' && chkItem[0] !== null) {
                        value = chkItem[0].value;
                    } else {
                        value = chkItem[0];
                    }
                }
            }
            else {
                var chkItem = $.grep(array, function (element, index) {
                    return (String(element.value).toLowerCase() == 'no' || String(element.value).toLowerCase() == '0' || String(element.value).toLowerCase() == 'false' ||
                        String(element.value).toLowerCase() == 'private' || String(element.value).toLowerCase() == 'inventory');
                });
                if (chkItem != null && chkItem.length > 0) {
                    if (typeof chkItem[0] === 'object' && chkItem[0] !== null) {
                        value = chkItem[0].value;
                    } else {
                        value = chkItem[0];
                    }
                }
            }
            if (value == "0") {
                value = 0;
            }
            else if (value == "1") {
                value = 1;
            }
            item.value = value;
            vm.showDependentItem(item, value, parentIndex, index)
        },
        clearRateIfNameEmpty: function (item, type, isInPercentage) {
            if ($.trim(item.TAX_NAME) == '') {
                item.TAX_RATE = 0;
            } else {
                this.isTaxNameFilled = false;
            }
        },
        outside: function (e) {
            // $.each(this.purchaseOrderDetails.SummaryData, function (key, item) {
            //     item.toggleTax = false;
            // });
        },
        onlyDecimal: function (item, type, isInPercentage) {
            if (type == 'TAX') {
                if (item.TAX_NAME != '') {
                    item.TAX_RATE = item.TAX_RATE.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');
                    if (isInPercentage && parseFloat((item.TAX_RATE != '') ? item.TAX_RATE : 0) > 100) {
                        item.TAX_RATE = '100';
                    }
                } else {
                    this.isTaxNameFilled = true;
                    item.TAX_RATE = "0";
                }
            } else if (type == 'DISCOUNT') {
                item.ProductDiscountValue = item.ProductDiscountValue.replace(/[^0-9.]/g, '').replace(/(\..*)\./g, '$1');
                if (isInPercentage && parseFloat((item.ProductDiscountValue != '') ? item.ProductDiscountValue : 0) > 100) {
                    item.ProductDiscountValue = '100';
                }
            }
        },
        DeleteProductSummaryItem: function (item, index) {
            var vm = this;
            if (index != 0) {
                vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("Delete") + "  " + vm.$t("ProductSummary")), function () {
                    vm.purchaseOrderDetails.SummaryData.splice(index, 1);
                });
            }
            else {
                vm.ShowAlert(vm.$t('DeleteAtleastOneProductMessage'),"warning", true, vm.$t("Alert"));
            }
        },
        CheckRequestQuantity: function (item) {
            if (item.ProductQuantity < parseInt(item.InwardQuantity)) {
                vm.ShowAlert(vm.$t("Received Quantity can not be greater than Requested Quantity."),"warning", true, vm.$t("Alert"));
            }
        },
        CalculateAmount: function (item) {
            if (item.InwardQuantity >= 0 && item.InwardUnitPrice >= 0) {
                item.ProductAmount = parseFloat(item.InwardQuantity, 10) * parseFloat(item.InwardUnitPrice, 10);
            }
        },
        onKeyDown: function () {
            checkForAnyChange = true;
        },
        isNumber: function (evt) {
           var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();
            } else {
                return true;
            }
        },
        onSearch: function onSearch(search, objItem) {
            this.searchSelectData(search, this, objItem);
        },
        searchSelectData: function (search, vm, objItem) {
                this.ShowSearchData(search, vm, objItem, objItem.value.value);
            },
            ShowSearchData(search, vm, objItem, selctedValue) {               
                if (typeof (selctedValue) == 'undefined') {
                    selctedValue = 0;
                }
                var url =  "id=&moduleName="+vm.$route.params.moduleName+"&type=" + objItem.field_code;
                if (objItem.dependent_type == 'fill') {
                    url += "&refId=" + selctedValue;
                }                
                LeadDataService.getDDLData(url + "&search=" + escape(search)).then(response => {
                   
                    var json = response.data;                   
                    if (json != null) {
                        objItem.select_options = json.DATA;
                        return objItem.select_options;
                    }                   
                });
            },
        FillDropdown: async function (type, refId) {
           var vm=this;
           vm.Loading= true;
            var search = '';
            var typeCode = type;
            var url = "id=&moduleName=" + this.$route.params.moduleName + "&type=" + typeCode + "&refId=" + refId;
           await LeadDataService.getDDLData(url + "&search=" + escape(search)).then(response => {          
            vm.Loading= false;    
            var json = [];
                if (response.data) {
                    json = response.data.DATA;
                    if (json[0].name == null || json[0].name == "") {
                        json = "";
                    }
                }

                if (type == "VENDOR_PURCHASE_ORDER") {
                    vm.purchaseOrderDetails = null;
                    vm.selectedPurchaseOrder = [];
                    vm.vendorPurchaseOrderList = json;
                        vm.tempVendorPurchaseOrderList = json;
                    var productSummary = [{
                        ProductName: '',
                        ProductList: [],
                        ProductDescription: '',
                        ProductQuantity: 0,
                        ProductInStock: 0,
                        ProductRate: 0,
                        ProductTax: 0,
                        ProductAmount: 0,
                        ProductDiscount: 0,
                        ProductDiscountType: 'percent',
                        ProductDiscountValue: 0,
                        ProductItemTotal: 0,
                        PriceBookId: 0,
                        toggleDiscount: false,
                        toggleTax: false,
                        InwardUnitPrice: 0,
                        receivedItem: 0,
                        InwardQuantity: 0,
                        purchaseOrderId: 0,
                        purchaseOrderNumber: '',
                        TaxData: [],
                        DiscountData: [],
                    }];
                    vm.purchaseOrderDetails = {
                        SubTotal: 0,
                        ShippingCharges: 0,
                        Adjustments: 0,
                        RoundOff: 0,
                        SummaryTotal: 0,
                        SummaryData: productSummary
                    }
                }
            });
        },
        setSelectedValue(item, val) {       
            var vm = this;
            // vm.$refs.form.setFieldTouched(item.name, false); 
            var selectedVal=val;
            try {
                if (Array.isArray(val)) {
                    selectedVal = val[0];
                }
                else {
                    selectedVal = val.value;
                    if (item.name == 'gin_vendor_id' && selectedVal > 0) {
                        vm.FillDropdown('VENDOR_PURCHASE_ORDER', val.value);
                    }
                }
            }
            catch (ex) {
            }
        },
        SubmitData( valid,errors) {
            var chkSummaryData = "";
            var vm = this;
            // vm.LeadData.forEach((item) => {
            //     if (item.astype != '' && item.form_field_visibility == 'YES') {
            //         if (item.dt_code == "select" && item.value) {
            //             try {
            //                 values[item.name] = String((typeof item.value === 'object' && item.value !== null) ? item.value.value : item.value);
            //                 vm.$refs.form.setFieldTouched(item.name, false);
            //             } catch (ex) { console.log('error while get dropdown parse data') }
            //         }
            //         if (item.dt_code == 'date' && item.value) {
            //             values[item.name] =vm.$options.filters.formatDate(item.value);
            //             vm.$refs.form.setFieldTouched(item.name, false);
            //         } else {
            //             vm.$refs.form.setFieldTouched(item.name, true);
            //         }
            //     }
            // })
            this.$refs.form.validate().then((result) => {
                if (result) {
                    vm.submited = true;
                    vm.isLoading = true;
                    var fullJson = vm.LeadData;
                  var data = [];
                  var validation = [];
                    $.each(fullJson, function (key, item) {
                        var validationObject = {
                            field_name: item.name,
                            required: item.is_required,
                            length: item.length,
                            type: item.validation_type
                        };
                        var fieldValue = (item.name == "gin_id" ? vm.inward_id : item.value);
                        if (item.dt_code == "date") {
                            fieldValue = item.value;
                        }
                        if (item.name == "gin_inward_type") {
                            try {
                                if (parseInt(item.value) == 0) {
                                    item.value = "Inventory";
                                } else if (parseInt(item.value) == 1) {
                                    item.value = "Asset";
                                } else if ($.trim(item.value) == "") {
                                    item.value = "Inventory";
                                }
                            } catch (ex) {
                            }
                        }
                        if (item.dt_code == "checkbox") {
                            var filtered = item.select_value.filter(function (el) {
                                return (el != null && el != "");
                            });
                            fieldValue = filtered.join(',');
                        }
                        if (item.dt_code == "select") {
                            try {
                                fieldValue = (typeof fieldValue === 'object' && fieldValue !== null) ? fieldValue.value : fieldValue
                            } catch (ex) { }
                        }
                        if(item.name=='created_by'){                
                              fieldValue = vm.userId;
                            }
                            if(item.name=='modified_by'){
                              fieldValue = vm.userId;
                            }
                            if(item.name=='company_id'){
                              fieldValue = vm.companyId;
                            }
                        var dataObject = {
                            field_name: item.name,
                            field_value: fieldValue,
                            custom_field_id: item.custom_field_id,
                        };
                        data.push(dataObject);
                        validation.push(validationObject);
                    });
                    var purchaseOrderDetailsFinal = []
                    var productSummaryData = [];
                    $.each(vm.purchaseOrderDetails.SummaryData, function (key, item) {
                        var projectId=null;
                        if (Array.isArray(item.ProductName)) {
                            if (typeof item.ProductName[0] === 'object' && item.ProductName[0] !== null) {
                                projectId = item.ProductName[0].value;
                            } else {
                                projectId = item.ProductName[0];
                            }
                        }
                        else {
                            projectId = item.ProductName.value;
                        }
                        if (projectId > 0) {
                            var dataObject = {
                                ProductName: projectId,
                                ProductDescription: item.ProductDescription,
                                ProductQuantity: item.ProductQuantity,
                                ProductRate: item.ProductRate,
                                ProductTax: item.ProductTax,
                                ProductAmount: item.ProductAmount,
                                ProductDiscount: item.ProductDiscount,
                                ProductDiscountType: item.ProductDiscountType,
                                ProductDiscountValue: item.ProductDiscountValue,
                                ProductItemTotal: item.ProductItemTotal,
                                InwardUnitPrice: item.InwardUnitPrice,
                                InwardQuantity: item.InwardQuantity,
                                purchaseOrderId: item.purchaseOrderId,
                                receivedItem: item.receivedItem,
                                purchaseOrderNumber: item.purchaseOrderNumber,
                                IsTaxApplicable: (item.TaxData != null && item.TaxData.length > 0 ? 1 : 0),
                                TaxData: item.TaxData,
                            };
                            productSummaryData.push(dataObject);
                        }
                    });
                    if (productSummaryData.length == 0) {
                        chkSummaryData = "no";
                    }
                    var purchaseOrderDetailsFinal = {
                        SubTotal: vm.purchaseOrderDetails.SubTotal,
                        ShippingCharges: vm.purchaseOrderDetails.ShippingCharges,
                        Adjustments: vm.purchaseOrderDetails.Adjustments,
                        SummaryTotal: vm.purchaseOrderDetails.SummaryTotal,
                        SummaryData: productSummaryData
                    }
                    var postJSON = {
                        data: data,
                        validation: validation,
                        ProductDetails: purchaseOrderDetailsFinal
                    };
                    if (chkSummaryData.length == 0) {
                        var objectPostString = JSON.stringify(postJSON);
                        LeadDataService.SaveGoodInward(objectPostString).then(response => {
                            vm.isLoading = false;
                            if (response.data[0].Code == "1") {
                                if (vm.inward_id > 0) {
                                    vm.ShowAlert(vm.$t('UpdatedSuccess',[vm.$t('GinInward')]), "success", true, vm.$t("Alert"));
                                }
                                else {
                                    vm. ShowAlert(vm.$t('AddedSuccess',[vm.$t('GinInward')]), "success", true, vm.$t("Alert"));
                                }
                                vm.$router.push({name:'InwardList'});
                            }
                            else {
                                vm.ShowAlert(vm.$t('AddedError',[vm.$t('GinInward')]), "failure", true, vm.$t("Alert"));
                            }
                            vm.submited = false;
                        });
                    }
                    else {
                        vm.ShowAlert(vm.$t("PleaseAddSomeLineItems"),"warning", true, vm.$t("Alert"));
                        vm.submited = false;

                    }
                }
            });
        },       
        FetchData: function () {
      
            var productSummary = [{
                ProductName: '',
                ProductList: [],
                ProductDescription: '',
                ProductQuantity: 0,
                ProductInStock: 0,
                ProductRate: 0,
                ProductTax: 0,
                ProductAmount: 0,
                ProductDiscount: 0,
                ProductDiscountType: 'percent',
                ProductDiscountValue: 0,
                ProductItemTotal: 0,
                PriceBookId: 0,
                toggleDiscount: false,
                toggleTax: false,
                InwardUnitPrice: 0,
                receivedItem: 0,
                InwardQuantity: 0,
                purchaseOrderId: 0,
                purchaseOrderNumber: '',
                TaxData: [],
                DiscountData: [],
            }];
            this.purchaseOrderDetails = {
                SubTotal: 0,
                ShippingCharges: 0,
                Adjustments: 0,
                RoundOff: 0,
                SummaryTotal: 0,
                SummaryData: productSummary
            }
            var vm = this;
            vm.isLoading=true;
            const params = `id=${vm.$route.params.id}&moduleName=CRM&subModuleCode=CRM_INWARD&langCode=en`;
            LeadDataService.getFormLayout(params).then(function (response) {
                vm.isLoading=false;
                vm.LeadData = response.data.data;
                //vm.GroupBy = vm.LeadData;
                if (vm.LeadData != null && vm.LeadData.length > 0) {
                    var groupId = vm.LeadData[0].group_id;
                    $.each(vm.LeadData, $.proxy(function (index, item) {
                        if (item.name == 'gin_inward_type' && item.value == '0') {
                            item.value = 'Inventory';
                        }
                        if (item.dt_code == 'checkbox') {
                            item.select_value = item.value.split(',');
                        }
                        if (item.dt_code == 'date') {
                                item.value= item.value==''?null:vm.$options.filters.formatDate(item.value);
                            }
                        groupId = item.group_id;
                        var chkGroup = $.grep(vm.GroupBy, function (element, index) {
                            return element.group_id == groupId;
                        });
                        if (chkGroup.length == 0) {
                            var returnedData = $.grep(vm.LeadData, function (element, index) {
                                return element.group_id == groupId;
                            });
                            if (vm.inward_id > 0) {
                                $.each(returnedData, function (filedIndex, fieldItem) {
                                    if (fieldItem.dt_code == "select") {
                                        try {
                                            fieldItem.value = fieldItem.value.trim();
                                        } catch (ex) { console.log("value is null while trimming") }
                                    }
                                    if (fieldItem.dropdown_type == "custom" && fieldItem.picklist_options == "Lookup") {
                                        fieldItem.select_options = vm.MakeNormalArray(fieldItem.select_options);
                                        fieldItem.select_value = fieldItem.select_options;
                                        if (fieldItem.select_options != null && fieldItem.select_options.length > 0) {
                                            fieldItem.value = fieldItem.select_options[0];
                                        }
                                    }
                                    else if (fieldItem.dt_code == "decimal" && fieldItem.form_field_visibility == "YES" && fieldItem.value > 0) {
                                        fieldItem.value = parseFloat(fieldItem.value, 10).toFixed(2);
                                    }
                                    else if (fieldItem.dt_code == "date") {
                                        fieldItem.value = vm.formatDate(fieldItem.value);
                                    }
                                });
                            }
                            else {
                                var objIndex = returnedData.findIndex((obj => obj.name == 'gin_number'));
                                if (objIndex != null && typeof (returnedData[objIndex]) != 'undefined') {
                                    returnedData[objIndex].value =vm.$t('InvoiceNumberGenratesAutomatically',{ 0: vm.$t("CRM_INWARD") });
                                }
                            }
                            if (returnedData.length > 0) {
                                var objGroup = {
                                    group_id: groupId,
                                    group_name: returnedData[0].group_name,
                                    group_display_name: returnedData[0].group_display_name,
                                    Data: returnedData
                                };
                                vm.GroupBy.push(objGroup);
                            }
                        }
                    }, vm));
                }
                if (vm.inward_id > 0) {
                    var productSummary = [];
                    var productSummaryAmount = response.data.productSummaryAmount;
                    var productSummaryList = response.data.productSummary;
                    $.each(productSummaryList, $.proxy(function (index, item) {
                        var checkItems = $.grep(vm.selectedPurchaseOrder, function (element, index) {
                            return element.value == vm.MakeNormalArray(item.purchaseOrderId)[0];
                        });
                        if (checkItems.length == 0 && item.purchaseOrderId) {                            
                            vm.selectedPurchaseOrder.push(vm.MakeNormalArray(item.purchaseOrderId)[0]);
                            vm.vendorPurchaseOrderList.push(vm.MakeNormalArray(item.purchaseOrderId)[0])
                        }
                        var obj = {
                            ProductName: vm.MakeNormalArray(item.product_name)[0],
                            ProductList: [],
                            ProductDescription: item.item_product_desc,
                            ProductQuantity: item.po_quantity,
                            ProductInStock: 0,
                            ProductRate: item.po_price,
                            ProductTax: 0,
                            ProductAmount: item.item_product_amount,
                            ProductItemTotal: 0,
                            toggleDiscount: false,
                            toggleTax: false,
                            InwardUnitPrice: item.gin_unit_price,
                            receivedItem: (item.recieved_quantity == null) ? 0 : item.recieved_quantity,
                            purchaseOrderId:item.purchaseOrderId? vm.MakeNormalArray(item.purchaseOrderId)[0].value:item.purchaseOrderId,
                            purchaseOrderNumber: item.order_number,
                            InwardQuantity: item.InwardQuantity,
                            TaxData: item.tax_data == null ? [] : item.item_product_amount * vm.MakeNormalArray(item.tax_data)[0].TAX_RATE / 100,
                            DiscountData: [],
                        };
                        productSummary.push(obj);
                    }, vm));
                    vm.purchaseOrderDetails = {
                        SubTotal: productSummaryAmount[0].summary_sub_total,
                        ShippingCharges: productSummaryAmount[0].summary_shipping_charges,
                        Adjustments: productSummaryAmount[0].summary_adjustments,
                        SummaryTotal: productSummaryAmount[0].summary_total,
                        SummaryData: productSummary
                    };
                }
                setTimeout(function(){vm.CheckBoxBootstrap()}, 500);
                setTimeout(function(){vm.RadioButtonBootstrap()}, 500);
                // setTimeout(function(){vm.ShowPopOver()}, 500);
                // if (vm.inward_id > 0) {
                //     var frmFields = {}
                //     vm.LeadData.forEach((item) => {
                //         if (item.astype != '' && item.form_field_visibility == 'YES') {
                //             frmFields[item.name] = item.value;
                //         }
                //     })
                //     console.log(frmFields);
                //     vm.$refs.form.resetForm({ values: frmFields });
                // }
            })
        },
        getProductDetailsForPurchaseOrder: function () {
            if (this.selectedPurchaseOrder != '' && this.selectedPurchaseOrder.length > 0) {
                var str = '';
                //-----------remove selectedpurchase order from list - added by avtar----------------------
                var selectedList = this.selectedPurchaseOrder;
                var tempList = $.grep(this.tempVendorPurchaseOrderList, function (element, index) {
                    return !(selectedList.includes(element));
                });
                this.vendorPurchaseOrderList = tempList;
                //---------------end start comment------------------
                $.each(this.selectedPurchaseOrder, function (index, item) {
                    str += (str == '') ? item.value : ',' + item.value;
                });

                var vm = this;               
                    var url = `sourceId=${this.$route.params.id}&moduleName=CRM&subModuleCode=CRM_PURCHASE_ORDERS&purchaseOrderIds=${str}`;
                    LeadDataService.getPurchaseOrderProductDetails(url).then(response => {                
                        if (response.data != null) {
                            var productSummary = [];
                            var productSummaryList = response.data.productSummary;
                            var objTax = {
                                        TAX_ID: 0,
                                        TAX_FOR: 'custom',
                                        TAX_TYPE: 'percent',
                                        TAX_RATE: 0,
                                        TAX_NAME: ''
                                    }
                            $.each(productSummaryList, $.proxy(function (index, item) {
                                var obj = {
                                    ProductName: vm.MakeNormalArray(item.product_name)[0],
                                    ProductList: [],
                                    ProductDescription: item.item_product_desc,
                                    ProductQuantity: item.item_product_Quantity,
                                    ProductInStock: 0,
                                    ProductRate: item.item_product_list_price,
                                    ProductTax: 0,
                                    ProductAmount: 0,
                                    ProductDiscount: item.item_product_discount,
                                    ProductDiscountType: item.item_product_discount_type,
                                    ProductDiscountValue: item.item_product_discount_value,
                                    ProductItemTotal: 0,
                                    PriceBookId: item.item_price_book_id,
                                    toggleDiscount: false,
                                    toggleTax: false,
                                    InwardUnitPrice: 0,
                                    receivedItem: item.received_quantity,
                                    purchaseOrderId: item.purchaseOrderId,
                                    purchaseOrderNumber: item.order_number,
                                    InwardQuantity: 0,
                                    TaxData: [],
                                    //TaxData: item.tax_data == null ? [] : item.item_product_amount * vm.MakeNormalArray(item.tax_data)[0].TAX_RATE / 100,
                                    DiscountData: [],
                                };
                                if(item.tax_data !=null){
                                    var TaxDataArray=vm.MakeNormalArray(item.tax_data);
                                    for(var i in TaxDataArray){
                                        TaxDataArray[i].TAX_FOR='custom';
                                        TaxDataArray[i].TAX_TYPE='percent';
                                        obj.TaxData.push(TaxDataArray[i])
                                    }
                                }
                                productSummary.push(obj);
                                console.log(productSummary);
                            }, vm));
                            vm.purchaseOrderDetails = {
                                SubTotal: 0,
                                ShippingCharges: 0,
                                Adjustments: 0,
                                SummaryTotal: 0,
                                SummaryData: productSummary
                            };
                        }
                    });
                
            } else {
                this.vendorPurchaseOrderList = this.tempVendorPurchaseOrderList
                var productSummary = [{
                    ProductName: '',
                    ProductList: [],
                    ProductDescription: '',
                    ProductQuantity: 0,
                    ProductInStock: 0,
                    ProductRate: 0,
                    ProductTax: 0,
                    ProductAmount: 0,
                    ProductDiscount: 0,
                    ProductDiscountType: 'percent',
                    ProductDiscountValue: 0,
                    ProductItemTotal: 0,
                    PriceBookId: 0,
                    toggleDiscount: false,
                    toggleTax: false,
                    InwardUnitPrice: 0,
                    receivedItem: 0,
                    InwardQuantity: 0,
                    purchaseOrderId: 0,
                    purchaseOrderNumber: '',
                    TaxData: [],
                    DiscountData: [],
                }];
                this.purchaseOrderDetails = {
                    SubTotal: 0,
                    ShippingCharges: 0,
                    Adjustments: 0,
                    RoundOff: 0,
                    SummaryTotal: 0,
                    SummaryData: productSummary
                }
            }
        },
        onDeselectPurchaseOrder:function(option){
this.vendorPurchaseOrderList.push(option);
if (this.selectedPurchaseOrder == '' && this.selectedPurchaseOrder.length == 0) {
    this.vendorPurchaseOrderList = this.tempVendorPurchaseOrderList
                var productSummary = [{
                    ProductName: '',
                    ProductList: [],
                    ProductDescription: '',
                    ProductQuantity: 0,
                    ProductInStock: 0,
                    ProductRate: 0,
                    ProductTax: 0,
                    ProductAmount: 0,
                    ProductDiscount: 0,
                    ProductDiscountType: 'percent',
                    ProductDiscountValue: 0,
                    ProductItemTotal: 0,
                    PriceBookId: 0,
                    toggleDiscount: false,
                    toggleTax: false,
                    InwardUnitPrice: 0,
                    receivedItem: 0,
                    InwardQuantity: 0,
                    purchaseOrderId: 0,
                    purchaseOrderNumber: '',
                    TaxData: [],
                    DiscountData: [],
                }];
                this.purchaseOrderDetails = {
                    SubTotal: 0,
                    ShippingCharges: 0,
                    Adjustments: 0,
                    RoundOff: 0,
                    SummaryTotal: 0,
                    SummaryData: productSummary
                }
}
        },
        totalSumlineItem: function (item) {
            var productAmount = 0;
        if (item.InwardUnitPrice > 0 && item.InwardQuantity > 0) {
                productAmount = parseFloat(item.InwardUnitPrice, 10);
            }
            productAmount = parseFloat(productAmount, 10) * item.InwardQuantity;
            if (item.TaxData != null && item.TaxData.length > 0 && item.InwardUnitPrice > 0 && item.InwardQuantity > 0) {
                var totalTax = 0;
                $.each(item.TaxData, $.proxy(function (index, taxItem) {
                    var taxrate = parseFloat(taxItem.TAX_RATE, 10);
                    if (taxItem.TAX_TYPE == "percent") {
                        if (taxrate > 0) {
                            var c = (parseFloat(productAmount, 10) * parseFloat(taxrate, 10)) / 100;
                            totalTax += parseFloat(c, 10);
                        }
                    }
                    else {
                        totalTax += parseFloat(taxrate, 10);
                    }
                }, this));
                if (totalTax > 0) {
                    totalTax = parseFloat(totalTax, 10);
                    productAmount = parseFloat(productAmount, 10) + parseFloat(totalTax, 10);
                    item.ProductTax = totalTax;
                }
            }
            var total = 0;
            total = (parseFloat(item.ProductAmount, 10) + parseFloat(item.ProductTax, 10)); //- (parseFloat(item.ProductDiscount, 10));
            item.ProductItemTotal = parseFloat(total, 10).toFixed2();
            return total;
        },
        summaryTotal: function (index) {
            let total = 0;
            var data = this.purchaseOrderDetails.SummaryData;
            if (typeof data !== 'undefined') {
                for (let item of data) {
                    if (item.InwardUnitPrice > 0) {
                        total = parseFloat(total, 10) + parseFloat(item.ProductItemTotal, 10);
                    }
                }
                total = parseFloat(total, 10).toFixed2();
                this.purchaseOrderDetails.SubTotal = total;
                if (this.purchaseOrderDetails.ShippingCharges > 0) {
                    total = parseFloat(total, 10) + parseFloat(this.purchaseOrderDetails.ShippingCharges, 10);
                }
                if (this.purchaseOrderDetails.Adjustments > 0) {
                    total = parseFloat(total, 10) - parseFloat(this.purchaseOrderDetails.Adjustments, 10);
                }
                total = parseFloat(total, 10).toFixed2();
                this.purchaseOrderDetails.SummaryTotal = total;
            }
            return total;
        },
        ShowTax: function (item) {
            item.toggleTax = !item.toggleTax;
            //ShowTaxItems(item);
        },
        AddMoreTax: function (item) {
            var objTax = {
                TAX_ID: 0,
                TAX_FOR: 'custom',
                TAX_TYPE: 'percent',
                TAX_RATE: 0,
                TAX_NAME: ''
            }
            item.TaxData.push(objTax);
        },
        DeleteMoreTax: function (item, taxItemIndex) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("Delete") + "  " + vm.$t("Tax")), function () {
                item.TaxData.splice(taxItemIndex, 1);
                item.ProductTax = '0.00';
            });
        },
    }
}
</script>
<template>
<section class="main-content">
            <loader :is-visible="isLoading"></loader>
            <div id="divMainLeadData" v-cloak>
<div class="row">
                    <div class="col-md-12 padding-t_8">
                        <div class="theme-primary partition-full">
                            <span class="p-name text-white">{{$t('Manage')}} Inward</span>
                            <span class="float-right">
                               
                            </span>
                        </div>
                    </div>
        </div>
<div class="border p-3">
                    <Form v-slot="{  valid,errors}" ref="form">
                        <DynamicForm :schema="GroupBy" :dateformat="dateformat"
                            v-on:AssignToggleValues="AssignToggleValues" v-on:setSelectedValue="setSelectedValue" v-on:onSearchDDL="onSearch"/>
                        <div class="row">
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left my-2">
                                <div class="heading-border">
                                    <h5 class="h5"><span>{{$t('ProductSummary')}}</span> </h5>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0 step1fixer my-2">
                                <div class="col-sm-12 col-md-6 float-left">
                                    <label>{{$t('PurchaseOrderWithoutRef')}} <span class="text-danger">*</span></label>
                                    <Field name="PurchaseOrderDDL" :rules="{'required':true}" label="Purchase Order"  v-slot="{errors,touched,invalid}">
                                    <Multiselect name="PurchaseOrderDDL"
                                          v-model="selectedPurchaseOrder" 
                                          :multiple="true" :taggable="true"
                                          :close-on-select="false" 
                                          :searchable="true"  :show-labels="false"
                                           track-by="value" :object="true" label="name"
                                            :options="vendorPurchaseOrderList" @close="getProductDetailsForPurchaseOrder"
                                            @deselect="onDeselectPurchaseOrder"
                                            v-bind:class="{ 'custom-vselect': submited }"></Multiselect>
                                        <span    name="PurchaseOrderDDL"  class="invalid-feedback d-block">{{errors[0]}}</span>         
                                    </Field>
                                </div>
                            </div>
                            <div class="listing p-3">
                                <div class="table-responsive mb-3">
                                    <table id="manageInwardTable" class="table table-bordered m-0" style="width:100%" aria-describedby="manageInward">
                                        <thead class="thead-bg">
                                            <tr class="text-capitalize">
                                                 <th scope="col" class="nonresize text-center" data-width="50">
                                                    <span>{{$t('SerialNumbering')}}</span></th>
                                                 <th scope="col" data-width="300" class="nonresize" style="min-width:250px">
                                                    <span>{{$t('PRODUCT_DETAILS')}}</span></th>
                                                 <th scope="col" style="min-width:150px;">
                                                    <span>{{$t('PURCHASE_ORDER_NUMBER')}}</span></th>
                                                 <th scope="col" style="min-width:150px;"><span>{{$t('RequestedPrice')}}</span></th>
                                                 <th scope="col" style="min-width:140px;"><span>{{$t('RequestedQuantity')}}</span>
                                                </th>
                                                 <th scope="col" style="min-width:140px;"><span>{{$t('ReceivedTillnow')}}</span></th>
                                                 <th scope="col" style="min-width:150px;"><span>{{$t('ListPrice')}}</span></th>
                                                 <th scope="col" style="min-width:140px;"><span>{{$t('Received')}} {{$t('Quantity')}}
                                                    </span></th>
                                                 <th scope="col" style="min-width:100px;"><span>{{$t('Amount')}}</span></th>
                                                 <th scope="col" colspan="2"><span>{{$t('Tax')}}</span></th>
                                                 <th scope="col"><span>{{$t('Total')}}</span></th>
                                                 <th scope="col"><span>{{$t('Action')}}</span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td class="text-danger text-center no-record" colspan="12"
                                                    v-if="purchaseOrderDetails.SummaryData == null || purchaseOrderDetails.SummaryData.length==0">
                                                    {{$t('NoRecordfound')}}
                                                </td>
                                            </tr>
                                            <tr v-for="(item,index) in purchaseOrderDetails.SummaryData" :key="index">
                                                <td class="text-center">{{index+1}}.</td>
                                                <td class="overflow-visible">
                                                    <Multiselect name="PurchaseOrderDDL"
                                                          v-model="item.ProductName"
                                                          noOptionsText=""
                                                          :placeholder="$t('SelectNameMultiSelect',[$t('ProductName')])"                                                 
                                                         track-by="name"
                                                        :options="item.ProductList" :object="true" label="name" 
                                                        class="disabled"                                                 
                                                        ></Multiselect>
                                                    <textarea class="form-control mt-2" type="text"
                                                        :placeholder="$t('EnterDescription')"
                                                        v-if="item.ProductName != '' && item.ProductName != null"
                                                        v-model="item.ProductDescription"></textarea>
                                                </td>
                                                <td>
                                                    <span class="float-left">{{item.purchaseOrderNumber}}</span>
                                                </td>
                                                <td>
                                                    <span class="float-left">{{item.ProductRate}}</span>
                                                </td>
                                                <td>
                                                    <span class="float-left">{{item.ProductQuantity}}</span>
                                                </td>
                                                <td>
                                                    <span class="float-left">{{item.receivedItem}}</span>
                                                </td>
                                                <td>
                                                    <input class="form-control" type="text"
                                                        v-bind:disabled="item.ProductName == '' || item.ProductName == null"
                                                        v-model="item.InwardUnitPrice"
                                                        v-on:input="CalculateAmount(item)" maxlength="12" />
                                                </td>
                                                <td>
                                                    <input class="form-control" type="text"
                                                        v-bind:disabled="item.ProductName == '' || item.ProductName == null"
                                                        v-model="item.InwardQuantity" v-on:input="CalculateAmount(item)"
                                                        v-on:keyup="CheckRequestQuantity(item)" maxlength="8" />
                                                </td>
                                                <td>
                                                    <span class="float-left">{{item.ProductAmount }}</span>
                                                </td>
                                                <td class="position-relative">
                                                    <span class="float-left">{{item.ProductTax }}</span>
                                                </td>
                                                <td
                                                    class="text-center overflow-visible position-relative single-action">
                                                    <a href="javascript:;" class="d-inline-block" :title="$t('Add')"
                                                        v-if="item.ProductName != '' && item.ProductName != null"
                                                        v-on:click="ShowTax(item)"><em class="fa fa-arrow-circle-right text-info"
                                                            style="font-size:20px;"></em></a>
                                                    <div class="position-absolute panelcoming paneltax panelfix bg-light"
                                                        v-if='item.toggleTax' v-click-outside="outside">
                                                        <a href="javascript:;" class="close pt-0"
                                                            style="font-size:23px; right: -20px; top: 10px;" data-dismiss="alert"
                                                            v-on:click="ShowTax(item)">&times;</a>
                                                        <div class="col-12 float-left p-0">
                                                            <div class="table-responsive">
                                                                <table id="tablelistingdata"
                                                                    class="table table-bordered mt-0"
                                                                    style="width:100%" aria-describedby="listingdata">
                                                                    <thead class="thead-bg">
                                                                        <tr>
                                                                             <th scope="col" width="150px"><span>{{$t('Tax')}}
                                                                                    {{$t('Percentagesign')}}
                                                                                </span></th>
                                                                             <th scope="col" width="150px"><span>{{$t('TaxFor')}}
                                                                                </span></th>
                                                                             <th scope="col" width="150px">
                                                                                <span>{{$t('Rate')}}</span></th>
                                                                             <th scope="col" class="text-center"><a
                                                                                    href="javascript:;"
                                                                                    v-on:click="AddMoreTax(item)"><em class="fa fa-plus-circle text-success"
                                                                                        style="font-size:20px;"></em></a>
                                                                            </th>
                                                                        </tr>
                                                                    </thead>
                                                                    <tbody>
                                                                        <tr v-for="(taxItem, taxItemIndex) in item.TaxData"
                                                                            :key="taxItemIndex">
                                                                            <td>
                                                                                <span
                                                                                    v-if="taxItem.TAX_ID>0">{{taxItem.TAX_NAME}}</span>
                                                                                <input type="text"
                                                                                    v-model="taxItem.TAX_NAME"
                                                                                    v-bind:class="{'form-control':true, 'is-invalid':isTaxNameFilled}"
                                                                                    v-if="taxItem.TAX_ID<=0"
                                                                                    @input="clearRateIfNameEmpty(taxItem, 'TAX', true)" />
                                                                            </td>
                                                                            <td>{{taxItem.TAX_FOR}}</td>
                                                                            <td>
                                                                                <div class="input-group">
                                                                                    <input type="text"
                                                                                        v-model="taxItem.TAX_RATE"
                                                                                        class="form-control col-9 float-left"
                                                                                        @input="onlyDecimal(taxItem, 'TAX', true)" />
                                                                                    <div class="input-group-append">
                                                                                        <span
                                                                                            v-if="taxItem.TAX_TYPE=='percent'"
                                                                                            class="input-group-text">%</span>
                                                                                       
                                                                                    </div>
                                                                                </div>
                                                                            </td>
                                                                            <td class="text-center single-action"
                                                                                v-if="taxItem.TAX_ID<=0">
                                                                                <a href="javascript:;"
                                                                                    v-on:click="DeleteMoreTax(item,taxItemIndex)"><em                                                    class="fa fa-trash text-danger"
                                                                                        style="font-size:20px;"></em></a>
                                                                            </td>
                                                                        </tr>
                                                                        <tr
                                                                            v-if="item.TaxData == null || item.TaxData.length==0">
                                                                            <td colspan="4"
                                                                                class="text-danger text-center no-record">
                                                                                {{$t('NoRecordfound')}} </td>
                                                                        </tr>
                                                                    </tbody>
                                                                </table>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </td>
                                                <td><span>{{totalSumlineItem(item)}}</span></td>
                                                <td class="text-center single-action">
                                                    <a href="javascript:;"
                                                        v-on:click="DeleteProductSummaryItem(item, index)"><em class="fa fa-trash action-icon text-danger"></em></a>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td colspan="11"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('SubTotal')}}:</td>
                                                <td colspan="2" class="border-bottom-0 align-middle">
                                                    <span>{{purchaseOrderDetails.SubTotal}}</span>
                                                </td>
                                            </tr>
                                            <tr>
                                                <td colspan="11"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('ShippingCharges')}}:</td>
                                                <td colspan="2" class="border-bottom-0"><input class="form-control"
                                                        type="text" placeholder="0.00" v-on:keypress="isNumber($event)"
                                                        maxlength="8" v-model="purchaseOrderDetails.ShippingCharges">
                                                </td>
                                            </tr>
                                            <tr>
                                                <td colspan="11"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('Adjustments')}}:</td>
                                                <td colspan="2" class="border-bottom-0"><input class="form-control"
                                                        type="text" placeholder="0.00" v-on:keypress="isNumber($event)"
                                                        maxlength="8" v-model="purchaseOrderDetails.Adjustments"></td>
                                            </tr>
                                            <tr>
                                                <td colspan="11"
                                                    class="text-dark text-right font-weight-bold align-middle border-bottom-0">
                                                    {{$t('Total')}}:</td>
                                                <td colspan="2" class="border-bottom-0"><span
                                                        v-bind:value="summaryTotal()">{{purchaseOrderDetails.SummaryTotal}}</span>
