                                <a href="javascript:;"
                                    data-tour="step: 1; title: enter DealDetail; content: AddDealDetailContent"
                                    v-bind:class="{ disabled: false }" v-on:click="SubmitData( valid,errors)"
                                    class="btn btn-success" title="Save">
                                    <em class="fa fa-save pr-2"></em>Save
                                </a>&nbsp;
                                <a href="javascript:void(0)" class="btn btn-danger" title="Cancel" @click="onCancel">
                                    <em class="fa fa-close pr-2"></em>Cancel
                                </a>
                            </div>
                            <div class="col-lg-6">
                                <div class="py-2">
<small class="text-danger">{{$t('MandatoryString')}}</small>
                                </div>
                            </div>
                        </div>
                    </Form>
                </div>
            </div>
        </section>
     
</template>
<style>
.disabled {
    color: #8e9894!important;
    cursor: not-allowed !important;
    pointer-events: none;
}

    .switch {
        width: auto !important;
        overflow: unset !important;
    }
        .switch .slider {
            left: auto !important;
            right: auto !important;
            padding: 2px 7px 0 8px;
        }
        .switch input:checked + .slider::before {
            -webkit-transform: translateX(26px);
            -ms-transform: translateX(26px);
            transform: translateX(26px);
            left: auto;
            right: 30px;
        }
 
</style>